[class*=js-go-to] {
  display: block;
  width: 3rem;
  height: 3rem;
  background-color: rgba(255, 255, 255, 0.66);
  color: #66C4C5;
  border-radius: 50%;
  box-shadow: 0 0 20px 0 rgba(31, 31, 57, 0.16);
  transition: all 0.25s ease-in-out;
  z-index: 100;
}
[class*=js-go-to] i {
  font-size: 2.75rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
[class*=js-go-to]:hover, [class*=js-go-to]:focus:hover {
  color: #4a828d;
}

.ribbon {
  position: relative;
}
.ribbon .ribbon-label {
  display: inline-block;
  vertical-align: middle;
  padding: 5px 10px;
  position: absolute;
  z-index: 1;
  background-color: #66C4C5;
  box-shadow: 0px -1px 5px 0px rgba(0, 0, 0, 0.1);
  color: #66C4C5;
  top: 50%;
  right: 0;
  transform: translateX(5px) translateY(-50%);
}
.ribbon .ribbon-label > .ribbon-inner {
  z-index: -1;
  position: absolute;
  padding: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.ribbon .ribbon-label:after {
  border-color: #28696a;
}
.ribbon-vertical .ribbon-label {
  padding: 5px 10px;
  min-width: 36px;
  min-height: 46px;
  text-align: center;
}
.ribbon.ribbon-top .ribbon-label {
  top: 0;
  transform: translateX(-15px) translateY(-4px);
  border-bottom-right-radius: 0.375rem;
  border-bottom-left-radius: 0.375rem;
}
.ribbon.ribbon-bottom .ribbon-label {
  border-top-right-radius: 0.375rem;
  border-top-left-radius: 0.375rem;
}
.ribbon.ribbon-start .ribbon-label {
  top: 50%;
  left: 0;
  right: auto;
  transform: translateX(-5px) translateY(-50%);
  border-top-right-radius: 0.375rem;
  border-bottom-right-radius: 0.375rem;
}
.ribbon.ribbon-end .ribbon-label {
  border-top-left-radius: 0.375rem;
  border-bottom-left-radius: 0.375rem;
}

.ribbon.ribbon-clip.ribbon-start .ribbon-label {
  left: -5px;
}
.ribbon.ribbon-clip.ribbon-start .ribbon-label .ribbon-inner {
  border-top-right-radius: 0.375rem;
  border-bottom-right-radius: 0.375rem;
}
.ribbon.ribbon-clip.ribbon-start .ribbon-label .ribbon-inner:before, .ribbon.ribbon-clip.ribbon-start .ribbon-label .ribbon-inner:after {
  content: "";
  position: absolute;
  border-style: solid;
  border-color: transparent !important;
  bottom: -10px;
}
.ribbon.ribbon-clip.ribbon-start .ribbon-label .ribbon-inner:before {
  border-width: 0 10px 10px 0;
  border-right-color: #1F1F39 !important;
  left: 0;
}
.ribbon.ribbon-clip.ribbon-end .ribbon-label {
