.separator {
  display: block;
  height: 0;
  border-bottom: 1px solid #F2F2F2;
}
.separator.separator-dotted {
  border-bottom-style: dotted;
  border-bottom-color: #E8E9EB;
}
.separator.separator-dashed {
  border-bottom-style: dashed;
  border-bottom-color: #E8E9EB;
}
.separator.separator-content {
  display: inline-block;
  vertical-align: middle;
  border-bottom: 0;
  text-align: center;
}
.separator.separator-content::before, .separator.separator-content::after {
  content: "";
  width: 50%;
  border-bottom: 1px solid #F2F2F2;
}
.separator.separator-content::before {
  margin-right: 1.25rem;
}
.separator.separator-content::after {
  margin-left: 1.25rem;
}
.separator.separator-content.separator-dotted::before, .separator.separator-content.separator-dotted::after {
  border-bottom-style: dotted;
  border-bottom-color: #E8E9EB;
}
.separator.separator-content.separator-dashed::before, .separator.separator-content.separator-dashed::after {
  border-bottom-style: dashed;
  border-bottom-color: #E8E9EB;
}
.separator.separator-content.border-primary::before, .separator.separator-content.border-primary::after {
  border-color: #66C4C5 !important;
}
.separator.separator-content.border-secondary::before, .separator.separator-content.border-secondary::after {
  border-color: #4F51C6 !important;
}
.separator.separator-content.border-tertiary::before, .separator.separator-content.border-tertiary::after {
  border-color: #FF9E9E !important;
}
.separator.separator-content.border-quaternary::before, .separator.separator-content.border-quaternary::after {
  border-color: #FEE482 !important;
}
.separator.separator-content.border-success::before, .separator.separator-content.border-success::after {
  border-color: #14927A !important;
}
.separator.separator-content.border-danger::before, .separator.separator-content.border-danger::after {
  border-color: #FF4B55 !important;
}
.separator.separator-content.border-warning::before, .separator.separator-content.border-warning::after {
  border-color: #FF9A3E !important;
}
.separator.separator-content.border-info::before, .separator.separator-content.border-info::after {
  border-color: #009EF7 !important;
}
.separator.separator-content.border-dark::before, .separator.separator-content.border-dark::after {
  border-color: #1F1F39 !important;
}
.separator.separator-content.border-grey::before, .separator.separator-content.border-grey::after {
  border-color: #5A6473 !important;
}
.separator.separator-content.border-lightgrey::before, .separator.separator-content.border-lightgrey::after {
  border-color: #9AA0A9 !important;
}
.separator.separator-content.border-light::before, .separator.separator-content.border-light::after {
  border-color: #B8B8D2 !important;
}
.separator.separator-content.border-white::before, .separator.separator-content.border-white::after {
  border-color: #fff !important;
}

.table--ver1 {
  background-color: #fff;
}
.table--ver1 thead {
  background-color: #8D949E;
  border-bottom: 2px solid #A4A9B1;
}
.table--ver1 th {
  font-weight: 700;
  padding: 0.75rem;
}
.table--ver1 tr:not(:last-child) {
  border-bottom: 1px dotted #A4A9B1;
}
.table--ver1 tr:last-child {
  border-bottom: 1px solid #A4A9B1;
}
.table--ver1 td {
  padding: 0.75rem;
}

.table--ver2 {
  background-color: #fff;
}
.table--ver2 thead {
  background-color: #D1D4D8;
  border-bottom: 2px solid #BBBFC5;
}
.table--ver2 tbody tr:nth-of-type(even) {
  background-color: #D1D4D8;
}
.table--ver2 th {
  border: 1px solid #BBBFC5;
  padding: 0.75rem;
}
.table--ver2 td {
  border: 1px solid #BBBFC5;
  padding: 0.75rem;
}

.table--ver3 {
  background-color: #fff;
}
.table--ver3 thead {
  border-bottom: 2px solid #F2F2F2;
  border-top: 2px solid #F2F2F2;
}
.table--ver3 th {
  border-top: 1px solid #F2F2F2;
  border-bottom: 1px solid #F2F2F2;
  padding: 0.75rem;
}
.table--ver3 td {
  border-top: 1px solid #F2F2F2;
  border-bottom: 1px solid #F2F2F2;
  padding: 0.75rem;
}

.table--dark {
  background-color: #606A78;
  color: #fff;
}
.table--dark thead {
  border-bottom: 2px solid #777F8B;
  border-top: 2px solid #777F8B;
}
.table--dark th {
  border-top: 1px solid #777F8B;
  border-bottom: 1px solid #777F8B;
  padding: 0.75rem;
}
.table--dark td {
  border-top: 1px solid #777F8B;
  border-bottom: 1px solid #777F8B;
  padding: 0.75rem;
}

.nav {
  margin-bottom: 0.5rem;
  padding-left: 0;
  list-style: none;
}
.ie .nav {
  *zoom: 1;
}
.nav:before, .nav:after {
  content: "";
  display: table;
}
.nav:after {
  clear: both;
  overflow: hidden;
}
.nav .nav-item {
  position: relative;
  display: block;
}
.nav .nav-item [data-toggle=tab] {
  position: relative;
  display: block;
  font-size: 1rem;
  cursor: pointer;
  padding: 0.625rem 1rem;
  transition: all 0.25s cubic-bezier(0.12, 0.49, 0.17, 0.87);
