.tu-toast {
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.24);
  color: #fff;
  min-width: 10rem;
  max-width: 20rem;
  padding: 1.5rem 1.75rem;
  text-align: center;
  user-select: none;
  pointer-events: none;
}
.tu-toast.float-toast {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.125s ease-in-out;
  animation-duration: 250ms;
  animation-fill-mode: both;
  z-index: 1100;
}
.tu-toast.toast--success {
  background-color: rgba(22, 123, 109, 0.48);
}
.tu-toast.toast--error {
  background-color: rgba(210, 66, 79, 0.48);
}
.tu-toast.toast--info {
  background-color: rgba(6, 133, 209, 0.48);
}
.tu-toast.toast--warning {
  background-color: rgba(210, 129, 61, 0.48);
}
.tu-toast.toast--help {
  background-color: rgba(88, 163, 169, 0.48);
}
.tu-toast.toast--default {
  background-color: rgba(31, 31, 57, 0.48);
}
.tu-toast:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 16px;
  background-color: inherit;
  background-attachment: fixed;
  backdrop-filter: blur(10px);
  filter: blur(10px);
}
.tu-toast > svg {
  position: relative;
  width: 3.5rem;
}
.tu-toast > i {
  position: relative;
  font-size: 3.5rem;
  margin-bottom: 0.75rem;
}
.tu-toast > font {
  position: relative;
  display: block;
  font-size: 1.125rem;
  color: #fff;
  margin-top: 0.5rem;
}

.toggle {
  background: #fff;
  border-radius: 4px;
  border: rgba(60, 60, 60, 0.125) 1px solid;
  display: inline-block;
  height: 1.875rem;
  margin-right: 0.5rem;
  vertical-align: middle;
  width: 5.25rem;
}
.toggle .toggle-checkbox {
  cursor: pointer;
  margin: 0;
  opacity: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}
.toggle .not-allowed {
  cursor: not-allowed;
}
.toggle .knobs,
.toggle .layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.toggle .knobs {
  z-index: 2;
}
.toggle .layer {
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
  width: 100%;
  z-index: 1;
}
.toggle .knobs:before {
  content: "開啟中";
  position: absolute;
  top: 0.125rem;
  left: 0.125rem;
  width: 3rem;
  height: 0.75rem;
  background-color: #1c84c6;
  color: #fff;
  line-height: 1;
  padding: 0.375rem 0.25rem;
  font-size: 12px;
  border-radius: 2px;
  text-align: center;
  transition: 0.3s ease all, left 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15);
}
.toggle .toggle-checkbox:checked + .knobs:before {
  content: "關閉中";
  left: 1.5rem;
  background-color: #b1b6bc;
}
.toggle .not-finish:checked + .knobs:before {
  content: "未完成";
  left: 1.5rem;
  background: #F5F6FA;
  color: #b1b6bc;
  cursor: not-allowed;
}
.toggle .knobs.orange:before {
  content: "同步中";
  background-color: #FF9A75;
}
.toggle .toggle-checkbox:checked + .knobs.orange:before {
  content: "非同步";
  left: 1.5rem;
  background-color: #b1b6bc;
}
.toggle .not-finish:checked + .knobs.orange:before {
  content: "未完成";
  left: 1.5rem;
  background: #F5F6FA;
  color: #b1b6bc;
  cursor: not-allowed;
}

