.range-slider-cnt {
  padding: 0.5rem 4rem;
  border-radius: 8px;
  background-color: rgba(31, 31, 57, 0.06);
  margin-bottom: 2rem;
}

.echart-tools {
  padding: 0.625rem 1rem 1.125rem;
  text-align: right;
}

.echart-container-sm {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  font-size: 1rem;
}
.echart-container-sm .echart-inner {
  height: 16rem;
}

.echart-container {
  background-color: #f8f8fb;
  position: relative;
  width: 100%;
  height: 100%;
  border: 1px solid #E8E9EB;
  border-radius: 8px;
  padding: 2rem;
  margin-bottom: 2rem;
  font-size: 1rem;
}
.echart-container .echart-inner {
  min-height: 32rem;
}
.echart-container .echart-inner div > svg {
  border-radius: 8px;
}
.echart-container.echart-container-1-1:before {
  content: "";
  display: block;
  width: 100%;
  padding-top: 100%;
}
@media all and (max-width: 992px) {
  .echart-container.echart-container-1-1:before {
    padding-top: 140%;
  }
}
.echart-container.echart-container-1-1 .echart-inner {
  min-height: auto;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 1rem;
}
.echart-container.echart-container-1-1 .echart-inner div > svg {
  border-radius: 8px;
}

.inner-dl {
  width: 50%;
  display: inline-block;
  vertical-align: middle;
  margin-top: 1rem;
  padding: 0.5rem;
  font-size: 0;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 10px 10px 25px #eaeaf2;
}
.inner-dl dt {
  border-radius: 16px 16px 0 0;
  background-color: #F5F5F6;
  border-bottom: 2px solid #eaeaf2;
  font-size: 1rem;
  font-weight: bolder;
  color: #302C4D;
  padding: 0.625rem;
  white-space: normal;
}
.inner-dl dt .img-container {
  margin-bottom: 0.75rem;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.36);
  filter: alpha(opacity=66);
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=66);
  opacity: 0.66;
}
.inner-dl dd {
  border-radius: 0 0 16px 16px;
  background-color: #F5F5F6;
  border-top: none;
  font-size: 1rem;
  padding: 0.25rem 0.75rem 0.75rem;
}
.inner-dl dd .js-counter {
  font-size: 1.125rem;
  font-weight: 700;
}

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Pages > maps
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */
#map_container {
  border-radius: 8px;
}

.map-content {
  position: relative;
  background-color: #f6f9fb;
  border-radius: 12px;
}
.map-content .tw-map {
  width: 100%;
  height: 824px;
}
.map-content path {
  cursor: pointer;
  transition: 0.25s all ease-in-out;
}
.map-content path:hover {
  fill: #1F1F39;
}
.map-content .svg-tooltip {
