.pace {
  pointer-events: none;
  user-select: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
  width: 100%;
  height: 6px;
  background: #fff;
}

.pace-inactive {
  display: none;
}

.pace .pace-progress {
  background-color: #66C4C5;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 100%;
  width: 100%;
  overflow: hidden;
  height: 6px;
}

.pace .pace-activity {
  position: fixed;
  top: 0;
  right: -32px;
  bottom: 0;
  left: 0;
  height: 6px;
  transform: translate3d(0, 0, 0);
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.2) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.2) 75%, transparent 75%, transparent);
  background-size: 32px 32px;
  animation: pace-theme-barber-shop-motion 500ms linear infinite;
}

@keyframes pace-theme-barber-shop-motion {
  0% {
    transform: none;
    transform: none;
  }
  100% {
    transform: translate(-32px, 0);
    transform: translate(-32px, 0);
  }
}
/** 
//  vendor > d3 map
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */
.d3-map-container {
  background-color: #F0EEE9;
  border-radius: 8px;
  position: relative;
  width: 100%;
}
.d3-map-container:before {
  content: "";
  display: block;
  width: 100%;
  padding-top: 100%;
}
.d3-map-container .d3-map-wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.d3-map-container .d3-map-tools {
  position: absolute;
  left: 1rem;
  top: 1rem;
  z-index: 1;
}
.d3-map-container .d3-map-tools .btn {
  margin-right: 0.375rem;
}
.d3-map-container .tw-map {
  width: 100%;
  height: 100%;
  filter: drop-shadow(6px 6px 0 rgba(59, 97, 113, 0.8));
}
.d3-map-container .d3-tooltips {
  position: absolute;
  text-align: center;
  padding: 0.5rem 1.25rem;
  font-size: 0.875rem;
  background-color: #1b1b26;
  color: #fff;
  filter: alpha(opacity=0);
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  opacity: 0;
  border: 0;
  border-radius: 0.5rem;
  pointer-events: none;
  text-anchor: middle;
  z-index: 1070;
  box-shadow: 0px 0px 50px 0px rgba(31, 31, 57, 0.26);
}
.d3-map-container .d3-tooltips:before {
  content: "";
  width: 1rem;
  height: 1.25rem;
  background-color: #2d4055;
  border-radius: 2px;
  position: absolute;
  bottom: -0.5rem;
  left: 50%;
  transform: rotate(45deg) translateX(-50%);
  z-index: -1;
}

.d3-map-path {
  stroke: #fff;
  stroke-width: 2;
  stroke-opacity: 0.66;
  font-size: 0.75rem;
  text-anchor: middle;
  transition: all 0.25s ease-in-out;
}
.d3-map-path:hover {
  background-color: #4F51C6;
}

.d3-map-country {
  position: relative;
  stroke: #fff;
  stroke-width: 2;
  stroke-opacity: 0.66;
  font-size: 0.75rem;
  text-anchor: middle;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}

.d3-map-txt {
  stroke: #fff;
  stroke-width: 2;
  paint-order: stroke;
  font-size: 0.875rem;
  text-anchor: middle;
  transition: all 0.25s ease-in-out;
  pointer-events: none;
  opacity: 0.82;
}

.d3-map-points {
  fill: #FF9E9E;
  stroke: rgba(255, 255, 255, 0.48);
  stroke-width: 2;
  transform: translateY(-1rem);
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}
.d3-map-points:hover {
  fill: #e4455e;
}
.d3-map-points:hover circle {
  r: 20;
}
.d3-map-points text {
  font-size: 0.875rem;
  font-weight: 700;
  stroke: #fff;
  paint-order: stroke;
  stroke-width: 3;
  fill: #1F1F39;
  text-anchor: middle;
}

.rs-container {
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  padding-bottom: 3rem;
  margin-right: 4rem;
  margin-bottom: 1.5rem;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  overflow: -moz-hidden-unscrollable;
  -ms-overflow-style: none;
  scroll-behavior: smooth;
  width: 100%;
  mask: linear-gradient(90deg, #1F1F39 95%, transparent);
}
.rs-container::-webkit-scrollbar {
  height: 0.625rem;
}
.rs-container::-webkit-scrollbar-track {
  background-color: transparent;
}
.rs-container::-webkit-scrollbar-thumb {
  border-radius: 4px;
  visibility: hidden;
}
.rs-container:hover::-webkit-scrollbar {
  height: 0.625rem;
}
.rs-container:hover::-webkit-scrollbar-track {
  background-color: rgba(90, 100, 115, 0);
}
.rs-container:hover::-webkit-scrollbar-thumb {
  background-color: rgba(90, 100, 115, 0.24);
  visibility: visible;
}
.rs-container:hover::-webkit-scrollbar-thumb:hover {
  background-color: rgba(90, 100, 115, 0.66);
}
.rs-container * {
  user-select: none;
}
.rs-container .rs-bg {
  background-color: #eaeaf2;
  border: 1px solid rgba(184, 184, 210, 0.48);
  border-radius: 4px;
  height: 0.75rem;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
}
.rs-container .rs-selected {
  background-color: #eaeaf2;
  border: 1px solid rgba(184, 184, 210, 0.48);
  border-radius: 2px;
  height: 0.75rem;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  background-color: #58a3a9;
  border: 1px solid #4a828d;
  transition: all 0.25s linear;
  width: 0;
}
.rs-container.disabled .rs-selected {
  background-color: #D1D4D8;
  border-color: #BBBFC5;
}
.rs-container .rs-pointer {
  background-color: #fff;
  border: 1px solid #BBBFC5;
  border-radius: 4px;
  box-shadow: inset 0 0 1px #fff, inset 0 1px 6px #E8E9EB, 1px 1px 4px rgba(31, 31, 57, 0.1);
  cursor: pointer;
  height: 1.25rem;
  width: 2rem;
  transition: all 0.2s linear;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.rs-container.disabled .rs-pointer {
  border-color: #D1D4D8;
  cursor: default;
}
.rs-container .rs-pointer::after, .rs-container .rs-pointer::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 0.5rem;
  background-color: #BBBFC5;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
}
.rs-container .rs-pointer::after {
  left: auto;
  right: 12px;
}
.rs-container.sliding .rs-pointer,
.rs-container.sliding .rs-selected {
  transition: none;
}
.rs-container .rs-scale {
  white-space: nowrap;
  position: relative;
  top: 2.5rem;
}
.rs-container .rs-scale span {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  padding: 1rem 0 0 0;
}
.rs-container .rs-scale span::before {
  content: "";
  background-color: rgba(184, 184, 210, 0.88);
  height: 0.625rem;
  width: 1px;
  position: absolute;
  left: 2px;
  top: 58%;
  transform: translateY(-50%);
}
.rs-container.rs-noscale span::before {
  display: none;
}
.rs-container.rs-noscale span:first-child::before, .rs-container.rs-noscale span:last-child::before {
  display: block;
}
.rs-container .rs-scale span:last-child {
  margin-left: -1px;
  width: 0;
}
.rs-container .rs-scale span ins {
  color: #1F1F39;
  display: inline-block;
  font-size: 0.75rem;
  margin-top: 1.5rem;
  text-decoration: none;
}
.rs-container.disabled .rs-scale span ins {
  color: #5A6473;
}

.rs-tooltip {
  color: #1F1F39;
  width: auto;
  min-width: 4rem;
  background: #fff;
  border: 2px solid rgba(102, 196, 197, 0.66);
  border-radius: 6px;
  position: absolute;
  transform: translate(-50%, -2.5rem);
  left: 0.75rem;
  text-align: center;
  font-size: 0.875rem;
  font-weight: 700;
  padding: 0.25rem 0.75rem;
}

.rs-container.disabled .rs-tooltip {
  border-color: #D1D4D8;
  color: #5A6473;
}